/**
* 
* baseUrl 
 * routerMode: 
 * imgBaseUrl: 
**/ 

let baseUrl = '',
    routerMode = 'hash',
    imgBaseUrl;


if (process.env.NODE_ENV == 'development') {
    // baseUrl = ''
} else if (process.env.NODE_ENV == 'production'){
    baseUrl = 'https://group.mrourou.com'
    imgBaseUrl = ''
}

export {
    baseUrl,
    routerMode,
    imgBaseUrl
}


